
.default-layout {
  height: 100%;
}

.container {
  position: relative;
  max-width: 450px;
  width: 100%;
  margin: 0 auto;

  @media (min-width: 450px) and (max-width: 700px) {
    max-width: 650px;
    padding: 0 15px;
  }
}

.main-box {
  display: flex;
  flex-direction: column;
  min-height: calc(100% - 126px);
}

.channels-modal {
  .close-btn {
    position: fixed !important;
  }
}

.empty-container {
  max-width: 320px;
  margin: 0 auto;
}

.app-header {
  padding: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &.empty {
    // position: absolute;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    top: -6px;
    left: 0;
    right: 0;
    z-index: calc(var(--zIndex) + 1);
  }

  .buttons-box {
    display: flex;
  }

  .channels-btn {
    background-color: whitesmoke;
    padding: 4px;
    border: none;
    border-radius: 9px;
    width: 40px;
    height: 40px;
    margin-right: 5px;
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      width: 66%;
      object-fit: contain;
    }
  }

  .about-btn {
    background-color: whitesmoke;
    padding: 7px 12px 8px;
    border: none;
    border-radius: 9px;
    min-width: 120px;
    height: 40px;
  }
}

.logo-box {
  width: 128px;
  margin-top: 4px;

  img {
    width: 100%;
  }
}


.close-btn {
  position: absolute;
  top: 10px;
  right: 24px;
  background-color: transparent;
  border: none;
  font-size: 30px;
  font-weight: bold;
  transform: rotate(45deg);

  &:active {
    transform: rotate(45deg) scale(0.9);
  }
}

.cookiePanel {
  position: fixed;
  width: 100%;
  bottom: 0;
  z-index: 2;
  background-color: #0057b7;
  color: #fff;
  padding: 10px;

  p {
    padding-top: 8px;
    // padding-left: 12px;
    width: 90%;
    font-size: 14px;
    text-align: justify;
  }

  .close-btn {
    color: #fff;
    right: 10px;
  }
}
